<template>
  
  <div>
    <i @click="showLeftBarFun" v-show="!showLeftBar"></i>
    <div class="leftBar" v-show="showLeftBar">
      <div class="choosetoplayleft">
          <div class="choosetheplay">{{english.选择玩法}}</div>
          <ul class="playList">
            <li v-for="(item,index) in methodsArr" @click="changePlay(index)" :key="index" :class="{'choosedactive':chekedMethod==index}">{{item}}</li>
              <!-- 选择玩法鼠标移入时显示的排名玩法列表 -->
              <div class="sort" style="display:none">
                  <ul>
                      <li>{{english.第一名}}</li>
                      <li>{{english.第二名}}</li>
                      <li>{{english.第三名}}</li>
                      <li>{{english.第四名}}</li>
                      <li>{{english.第五名}}</li>
                  </ul>
              </div>
          </ul>
        </div>
        <div class="choosetoplaycenter">
            <div class="centermain" id="centermain">
                <div class="top">
                    <ul>
                        <li :class="{activebottomline:!isZhuiHao}" @click="setZhuiHao(false)">{{english.投注}}</li>
                        <li :class="{activebottomline:isZhuiHao}" @click="setZhuiHao(true)">{{english.追号}}</li>
                    </ul>
                </div>
                <!-- 猜双面 -->
                <div class="center">
                    <ul>
                        <li>
                            <span></span>&nbsp;&nbsp;&nbsp;{{methodsArr[chekedMethod]}}</li>
                        <li @click="declareModel = true">
                            <em></em>&nbsp;{{english.赔率说明}}</li>
                    </ul>
                </div>
                <div class="bigorsmall">
                    <ul>
                      <template v-if="methodsArr[chekedMethod]==english.大小单双||methodsArr[chekedMethod]==english.特殊玩法">
                        <li v-for="(item,index) in playMethod" class="chooseSytle" :key="index" @click="checkOdds(index,item.title)" v-bind:class='{checkedStyle:checkedOddsStyle(index)}'>
                          <p>{{item.title}}</p>
                          <p>{{item.value}}</p>
                        </li>
                      </template>
                      <template v-else-if="methodsArr[chekedMethod]==english.猜数字">
                        <li v-for="(item,index) in playMethod" class="chooseSytle" :key="index" @click="checkOdds(index,item.title)" v-bind:class='{checkedStyle:checkedOddsStyle(index)}'>
                          <!-- 猜数字 -->
                          <div class="xyCszBox" :class="`xyCszBox${item.title}`">{{item.title}}</div>
                          <p>{{item.value}}</p>
                        </li>
                      </template>
                      <template v-else-if="methodsArr[chekedMethod]==english.长龙投注">
                        <div class="clTitle"><span>{{lDragonData[0].way}}</span><span>{{english.期}} {{lDragonData[0].num}}</span></div>
                        <li v-for="(item,index) in playMethod[0].data" class="chooseSytle" :key="index" @click="checkOdds(index,item.way)" v-bind:class='{checkedStyle:checkedOddsStyle(index)}'>
                          <p>{{item.way}}</p>
                          <p>{{item.odds}}</p>
                        </li>
                      </template>
                    </ul>
                </div>
            </div>
            <!-- 投注操作区 -->
            <div class="choosetoplayfooter" id="choosetoplayfooter" v-show="!isZhuiHao">
                <div class="havebet">
                    <p>{{english.已选}}&nbsp;
                        <span>{{checkedOdd.length}}</span>&nbsp;{{english.注}}, {{english.共}}&nbsp;
                        <span>{{totalMoney}}</span>&nbsp;{{english.元宝}}</p>
                </div>
                <div class="inputbet">
                    <InputNumber v-model="checkedMoney" @on-change="checkedMoneyIdx=null" :max="999999999" :min='1' :formatter="value=> `${Math.round(value)}`" :parser="value=> `${Math.round(value)}`" :placeholder="english.请输入下注金额" style="width:200px !important"></InputNumber>
                    <span @click="minBetAdd()">{{english.最小投注}}</span>
                </div>
                <div class="bet">
                    <ul>
                        <li v-for="(item,index) in moneyList" @click="checkMoney(index,item)" :key="index" v-bind:class='{ckMoneyStyle:index==checkedMoneyIdx}'>{{item>=1000 ? Number(item)/1000 + 'K' : item}}</li>
                        <li @click="checkMoney(100,english.梭哈)" v-bind:class='{ckMoneyStyle:checkedMoneyIdx==100}'>{{english.梭哈}}</li>
                    </ul>
                </div>
                <div class="reset">
                    <Button type="primary" @click="openOddsDlg()" :disabled="oddsWay.length<1" v-bind:class='{disabledBtn: checkedOdd.length<1}' class="qrBetBtn">{{english.确认下注}}</Button>
                    <Button type="primary" @click="resetOdds()">{{english.重置}}</Button>
                </div>
            </div>
            <!-- 追号操作区 -->
            <div class="zhPanel" id="zhPanel" v-show="isZhuiHao">
              <div class="zfInput">
                {{english.期}} <InputNumber type="text" :max="100" :min="1" placeholder="" v-model="manyQi" style=""></InputNumber>&nbsp;&nbsp;
                {{english.翻}} <InputNumber type="text" :min="1" placeholder="" v-model="manyBei" style=""></InputNumber>{{english.倍}}
              </div>
              <InputNumber type="text" :placeholder="english.请输入下注金额" class="moneyInput" v-model="checkedMoney"></InputNumber>                
              <div class="btnBox">
                <Button type="primary" @click="setZhDlg(true)">{{english.生成追号}}</Button>
              </div>
            </div>
            <!-- 点击收缩左边的两栏 -->
            <em class="shrink" @click="showLeftBarFun" v-show="showLeftBar"></em>
        </div>
        <Modal
          :scrollable="true"
          v-model="sendOddsDlg"
          class-name=""
          :closable="false"
          :mask-closable="false">
          <p slot="header">{{english.期}} {{issue}}, {{english.下注清单}}</p>
          <Table height="300" :columns="columns1" :data="oddsTableData" style="font-size:14px;" v-if="oddsTableData.length>0"></Table>
          <div class="tableNoData" v-else>
            <img src="@/assets/images/tableNoData.png" alt="">
          </div>
          <div slot="footer">
            <p class="tableTip">{{english.共}} <span>{{checkedOdd.length}}</span>{{english.注}}, {{english.金额}}<span>{{totalMoney}}</span><s></s></p>
            <Button @click="closeOddsDlg">{{english.取消}}</Button>
            <Button type="primary" @click="sendOdds" :disabled="oddsTableData.length<1||stopBet" :loading="sendFlag">{{english.确认投注}}</Button>
          </div>
        </Modal>
        <Modal
          :scrollable="true"
          v-model="showZhDlg"
          class-name=""
          :title="english.追号清单"
          width="600"
          :mask-closable="false">
          <div class="zhPanel2">
            {{english.期}} <InputNumber type="text" :max="100" :min="1" placeholder="" v-model="manyQi" style=""></InputNumber>&nbsp;&nbsp;
            {{english.翻}} <InputNumber type="text" :min="1" placeholder="" v-model="manyBei" style=""></InputNumber>{{english.倍}}
            <InputNumber type="text" :placeholder="english.请输入下注金额" class="moneyInput2" v-model="checkedMoney"></InputNumber>          
            <Button type="primary" @click="setZhDlg(true)">{{english.生成追号}}</Button>
          </div>
          <Table height="300" :columns="columns2" :data="zhTableData" style="font-size:14px;"></Table>
          <p class="zjjtP"><Checkbox v-model="zhType">{{english.中奖即停}}</Checkbox></p>
          <div slot="footer">
            <p class="tableTip">{{english.共}} <span>{{manyQiNow}}</span>{{english.注}}, {{english.金额}}<span>{{totalZhMoney}}</span></p>
            <Button @click="setZhDlg(false)">{{english.取消}}</Button>
            <Button type="primary" @click="confromZh()" :disabled="zhTableData.length<1||stopBet">{{english.确认投注}}</Button>
          </div>
        </Modal>
        <Modal
            v-model="declareModel"
            :scrollable="true"
            :title="english.赔率说明"
            @on-ok="declareModel = false"
            cancel-text
            :ok-text="english.关闭"
            width='600'
            >
            <p style="max-height: 500px;overflow-y: auto;">
              <pre v-html="RoomDateList"></pre>
            </p>
        </Modal>
    </div>
  </div>
</template>

<script>
import { websocketsend } from "@/common/js/websocket";
import _ from "lodash";
import { mapGetters, mapActions } from "vuex";
import removeImg from "@/assets/images/remove.png";
import { timeFormat } from "@/assets/js/dom";
import english from "@/assets/json/English.json";
export default {
  props: [
    "oddsData",
    "timeMessages",
    "cRoomData",
    "BetmoneyList",
    "bindAbsClass",
    "lDragonData"
  ],
  data() {
    return {
      english: english,
      sendFlag: false,
      playMethod: [],
      issue: "", //当前期号
      checkedOdd: [], //赔率
      moneyList: [5, 10, 50, 100, 500], //下注金额
      checkedMoney: 1, //金额
      checkedMoneyIdx: 0,
      checkedOddIndex: 0,
      oddsWay: [], //投注数组
      moneyArr: [], //金额数组
      totalMoney: 0,
      isZhuiHao: false, //是否是追号
      showZhDlg: false, //追号面板
      zhTableData: [], //追号tabledata
      zhBetData: [], //追号data
      zhType: true, //中奖即停
      manyQi: 1, //追几期
      manyQiNow: 1,
      manyBei: 1, //翻几倍
      totalZhMoney: 0, //追号总金额
      sendOddsDlg: false,
      chekedMethod: 0, //选中的玩法
      methodsArr: [english.大小单双,english.猜数字,english.特殊玩法],
      columns1: [
        //投注表格内容自定义
        {
          title: english.玩法,
          key: "name",
          align: "center"
        },
        {
          title: english.内容,
          key: "type",
          align: "center"
        },
        {
          title: english.金额,
          key: "money",
          align: "center",
          render: (h, params) => {
            return h("InputNumber", {
              props: {
                value: this.moneyArr[params.index],
                formatter: value => `${Math.round(value)}`,
                parser: value => `${Math.round(value)}`,
                max: 10000,
                min: 1
              },
              style: { width: "80px", textAlign: "center" },
              on: {
                input: e => {
                  e = Math.round(e);
                  this.totalMoney = 0;
                  this.$set(this.moneyArr, params.index, e);
                  for (var i = 0; i < this.moneyArr.length; i++) {
                    this.totalMoney += this.moneyArr[i];
                  }
                }
              }
            });
          }
        },
        {
          title: english.撤单,
          key: "remove",
          align: "center",
          render: (h, params) => {
            return h("img", {
              attrs: {
                src: removeImg
              },
              style: { cursor: "pointer", "vertical-align": "middle" },
              on: {
                click: () => {
                  this.totalMoney =
                    this.totalMoney - this.moneyArr[params.index];
                  this.oddsTableData.splice(params.index, 1);
                  this.moneyArr.splice(params.index, 1);
                  this.oddsWay.splice(params.index, 1);
                  this.checkedOdd.splice(params.index, 1);
                }
              }
            });
          }
        }
      ],
      columns2: [
        {
          title: english.期号,
          key: "issue",
          align: "center"
        },
        {
          title: english.投注金额,
          key: "money",
          align: "center"
        },
        {
          title: english.翻倍,
          key: "multiple",
          align: "center"
        }
      ],
      oddsTableData: [],
      declareModel: false, //
      RoomDateList: null, //房间信息
      roomId: null, //房间id
      betLower: 1,
      showLeftBar: true,
      token: localStorage.getItem('token')
    };
  },
  created() {
    this.roomId = this.$route.query.id
  },
  mounted() {},
  computed: {
    ...mapGetters(["accountData", "userInfo", "serTime", "stopBet"])
  },

  methods: {
    showLeftBarFun: function() {
      this.showLeftBar = !this.showLeftBar;
      setTimeout(() => {
        var chatcontent = document.getElementById("chatcontent");
        var sendmsg = document.getElementById("sendmsg");
        sendmsg.style.width = chatcontent.offsetWidth + "px";
      });
    },
    changePlay(id) {
      this.chekedMethod = id;
      this.resetOdds();
      if(this.methodsArr[id]==english.长龙投注){
          this.playMethod = this.lDragonData
      }else if(this.methodsArr[id]==english.大小单双){
          this.playMethod = this.oddsData.panel_1;
      }else if(this.methodsArr[id]==english.猜数字){
          this.playMethod = this.oddsData.panel_2;
      }else if(this.methodsArr[id]==english.特殊玩法){
          this.playMethod = this.oddsData.panel_3;
      }
    },
    setZhuiHao(flag) {
      //打开或关闭追号面板
      this.resetOdds();
      this.isZhuiHao = flag;
    },
    checkedOddsStyle: function(index) {
      //选中style
      if (this.isZhuiHao == true) {
        if (this.checkedOddIndex == index) {
          return true;
        }
      } else {
        if (this.checkedOdd.indexOf(index) != -1) {
          return true;
        }
      }
    },
    setZhDlg: function(flag) {
      //设置追号数据
      if (flag) {
        if (this.checkedOddIndex == -1) {
          this.$Message.info(english.请选择玩法);
          return;
        }
        var issue = this.issue;
        var zhTableData = [];
        var zhBetData = [];
        var totalZhMoney = 0;
        this.manyQi = Math.round(this.manyQi);
        this.manyQiNow = this.manyQi;
        if (this.manyQi > 100) {
          this.$Message.info(english.最多追加100期+"!");
          this.manyQi = 100;
          return;
        }
        this.manyBei = Math.round(this.manyBei);
        this.checkedMoney = Math.round(this.checkedMoney);
        for (var i = 1; i <= this.manyQi; i++) {
          totalZhMoney += this.checkedMoney * Math.pow(this.manyBei, i - 1);
          zhTableData.push({
            issue: issue,
            money: this.checkedMoney * Math.pow(this.manyBei, i - 1) + english.元宝,
            multiple: Math.pow(this.manyBei, i - 1) + english.倍
          });
          zhBetData.push({
            qihao: issue,
            money: this.checkedMoney * Math.pow(this.manyBei, i - 1),
            single_money: this.checkedMoney,
            way: this.playMethod[this.checkedOddIndex].title||this.playMethod[this.checkedOddIndex].way,
            multiple: Math.pow(this.manyBei, i - 1)
          });
          issue++;
        }
        this.totalZhMoney = totalZhMoney;
        this.zhTableData = zhTableData;
        this.zhBetData = zhBetData;
      }
      this.showZhDlg = flag;
    },
    confromZh: function() {
      //追号确认
      this.$Modal.confirm({
        content:
          "<p>"+english.本期追号投注+": " +
          english.期 + " " + this.manyQiNow +
          ", " + english.共 + " " +
          this.totalZhMoney +
          english.元宝+"</p>",
        scrollable: true,
        loading: true,
        onOk: () => {
          const agentData = {
            token: this.token,
            commandid: "3019",
            nickname: JSON.parse(localStorage.getItem("userData")).nickname,
            data: this.zhBetData,
            win_stop: this.zhType ? 1 : 0,
            avatar: JSON.parse(localStorage.getItem("userData")).avatar
          };
          websocketsend(agentData);
          this.showZhDlg = false;
        }
      });
    },
    checkOdds(index, title) {
      //选中操作
      this.checkedOddIndex = index;
      if (this.checkedOdd.indexOf(index) == -1) {
        this.checkedOdd.push(index);
        this.oddsWay.push(title);
        this.totalMoney = this.checkedOdd.length * this.checkedMoney;
      } else {
        for (var i = 0; i < this.checkedOdd.length; i++) {
          if (this.checkedOdd[i] == index) {
            this.checkedOdd.splice(i, 1);
            this.oddsWay.splice(i, 1);
            this.totalMoney = this.checkedOdd.length * this.checkedMoney;
          }
        }
      }
    },
    checkMoney(index, item) {
      if (item == english.梭哈) {
        let accountData = this.accountData.money_usable;
        this.checkedMoney = Math.round(Number(accountData));
        this.checkedMoneyIdx = index;
        this.totalMoney = this.checkedMoney * this.checkedOdd.length;
      } else {
        this.checkedMoney = item;
        this.checkedMoneyIdx = index;
        this.totalMoney = this.checkedMoney * this.checkedOdd.length;
      }
    },
    resetOdds() {
      this.zhBetData = [];
      this.zhTableData = [];
      this.manyBei = 1;
      this.manyQi = 1;
      this.checkedOddIndex = -1;
      this.checkedOdd = [];
      this.oddsWay = [];
      this.checkedMoney = this.moneyList[0];
      this.checkedMoneyIdx = 0;
      this.totalMoney = 0;
    },
    closeOddsDlg: function() {
      this.totalMoney = this.checkedMoney * this.checkedOdd.length;
      this.sendOddsDlg = false;
    },
    openOddsDlg: function() {
      let newMoneyArr = [];
      let newOddsArr = [];
      for (var i = 0; i < this.oddsWay.length; i++) {
        newOddsArr.push({
          name: this.methodsArr[this.chekedMethod],
          type: this.oddsWay[i],
          money: this.checkedMoney,
          remove: english.删除
        });
        newMoneyArr.push(this.checkedMoney);
      }
      this.moneyArr = newMoneyArr;
      this.oddsTableData = newOddsArr;
      this.sendOddsDlg = true;
    },
    sendOdds: function() {
      this.sendFlag=true
      setTimeout(()=>{
        this.sendFlag=false
      },1000)
      const agentData = {
        token: this.token,
        commandid: "3006",
        nickname: JSON.parse(localStorage.getItem("userData")).nickname,
        way: this.oddsWay,
        money: this.moneyArr,
        avatar: JSON.parse(localStorage.getItem("userData")).avatar
      };
      var way = this.oddsWay
      var money = this.moneyArr
      var uniNo = new Date().getTime()+Math.floor(Math.random()*900)+100
      var params = {
        user_id: this.userInfo.id,
        lottery_type: this.$route.query.lottery_type,
        room_id: this.$route.query.id,
        nickname: this.userInfo.nickname,
        money: JSON.stringify(money),
        issue: this.issue,
        way: JSON.stringify(way),
        serTime: this.serTime,
        uniNo: uniNo,
        token: this.token,
      }
      let lotRstData={
        isHttp: true,
        isLoading: true,
        avatar: this.userInfo.avatar,
        commandid: 30007,
        issue: this.issue,
        money: money,
        nickname: this.userInfo.nickname,
        time: timeFormat(new Date().getTime()),
        total_money: this.totalMoney,
        total_zushu: this.checkedOdd.length,
        uid: this.userInfo.id,
        uniNo: uniNo,
        sort: this.userInfo.honor.sort,
        serTime: this.serTime,
        order_no: '',
        way: way,
        loadingFailed: false,
        failedText: '',
      }
      this.$store.commit('setLotRstData', lotRstData)
      this.$http.post(this.urlRequest + "?m=api&c=workerman&a=http_betting", params).then(result=>{
        result=result.data
        if(result.commandid!=3007){
          var failedObj = JSON.parse(JSON.stringify(lotRstData))
          failedObj.loadingFailed=true
          failedObj.failedText=result.content
          this.$store.commit('setNewRstData', failedObj)
          return
        }
        var accountData = this.accountData
        accountData.money_usable=result.current_money
        this.$store.commit('setAccountData', accountData)
        this.$store.commit('setNewRstData', result)
      },
      err=>{
        lotRstData.loadingFailed=true
        lotRstData.failedText=english.投注超时+', '+english.请重新下注
        this.$store.commit('setNewRstData', lotRstData)
      })
      // websocketsend(agentData);
      this.sendOddsDlg = false;
    },
    minBetAdd() {
      // 最小投注的事件
      this.checkedMoney = Number(this.betLower);
      this.totalMoney = this.checkedMoney * this.checkedOdd.length;
      this.checkedMoneyIdx = null;
    }
  },
  components: {},
  watch: {
    oddsData: function(oldVal, newVal) {
      for(var key in oldVal){
        if(_.isEmpty(oldVal[key])){
          if(key=='panel_3'){
            this.methodsArr.splice(2,1)
          }
        }
      }
      this.playMethod = oldVal.panel_1;
    },
    lDragonData: function(newVal, oldVal){
      if(newVal){
        for(var i=0;i<this.methodsArr.length;i++){
          if(this.methodsArr[i]==english.长龙投注){
            this.methodsArr.splice(i,1)
          }
        }
        if(newVal.length>0){
          this.methodsArr.push(english.长龙投注)
          if(this.chekedMethod==3){
            this.playMethod = newVal[0].data;
          }
        }
      }
    },
    timeMessages: function(oldVal, newVal) {
      this.issue = oldVal.issue;
    },
    cRoomData: function(oldVal, newVal) {
      let dataArr = oldVal.p_room;
      for (var i = 0; i < dataArr.length; i++) {
        if (this.roomId == dataArr[i].id||this.roomId == dataArr[i].idd) {
          this.betLower = dataArr[i].lower;
          if (dataArr[i].odds_exp == undefined || dataArr[i].odds_exp == "") {
            this.RoomDateList = english.暂无数据;
          } else {
            this.RoomDateList = dataArr[i].odds_exp;
          }
        }
      }
    },
    BetmoneyList: function(oldVal, newVal) {
      // let arr = []
      // for(var i=0; i<oldVal.length; i++){
      //   if(oldVal[i]>=1000){
      //       arr = arr.push(Number(oldVal[i]) / 1000 + 'K')
      //   }else{
      //       arr.push(Number(oldVal[i]))
      //   }
      // }
      this.moneyList = oldVal;
      this.checkedMoney = this.moneyList[0];
    },
    checkedMoney: function(newVal, oldVal) {
      this.totalMoney = this.checkedOdd.length * newVal;
    }
  },
  deactivated() {},
  beforeDestroy() {}
};
</script>

<style scoped>

.oddsTable {
  width: 100%;
  line-height: 24px;
}
.oddsTable td {
  text-align: center;
}
.playList {
  font-size: 14px;
}
.chooseSytle:hover {
  background: #ffd6d1;
  border: 0.5px solid #f3596a !important;
}
.chooseSytle p:first-child {
  color: #48484c;
  font-size: 14px;
}
.chooseSytle p:last-child {
  color: #f3596a;
  line-height: 18px;
}
.checkedStyle {
  background: #ffd6d1;
  border: 0.5px solid #f3596a !important;
}
.checkedStyle p {
  color: #f3596a !important;
}
.ckMoneyStyle {
  position: relative;
  top: -6px;
  border-radius: 50%;
  box-shadow: 0px 0px 10px #FFEC00;
}
.choosetoplayleft {
  height: 760px;
  width: 90px;
  background-color: #f9f9f9;
  text-align: center;
  line-height: 25px;
}
.choosetoplayleft .choosetheplay {
  width: 90px;
  height: 50px;
  font-weight: 600;
  font-size: 16px;
}
