        }
      } else if (flag == 2) {
        //开奖历史
        window.open(
          this.imgRequest +
            "/pczs/Trendssc/trendHeader.html?trendType=history&lotteryId=" +
            this.lottery_type
        );
      } else if (flag == 3) {
        let lottery_list = this.getHeaderFooter.lottery_list;
