  top: 50px;
  right: -52px;
  color: white;
  text-align: center;
  cursor: pointer;
  transform: translateX(calc((100vw - 1200px) / 2));
  animation: rightSlide 1s forwards .2s;
}
.mipaibet span {
  background: url(../../../assets/images/mi.png)no-repeat;
