<template>
  <div>
    <i @click="showLeftBarFun" v-show="!showLeftBar"></i>
    <div  v-show="showLeftBar" class="leftBar">
    <div class="choosetoplayleft">
        <div class="choosetheplay">{{english.选择玩法}}</div>
        <ul class="playList">
          <li v-for="(item,index) in wanfOdds" :key="index"  :class="{'choosedactive':chekedMethod==(index+1)}">
              <Dropdown placement="right-start" v-if="index<2" @on-click="getodds">
                    <a href="javascript:void(0)" @click="navClick(item.name)">
                       {{item.name}}
                    </a>
                    <DropdownMenu slot="list">
                        <DropdownItem v-for="(itemChild,index) in item.set" :name="index+'_'+item.name" :key="index">{{english[itemChild]}}</DropdownItem>
                    </DropdownMenu>
                </Dropdown>
                <span class="otherf1" @click="changePlay(item.name)" v-else>
                    {{item.name}}
                </span>
          </li>
        </ul>
      </div>
      <div class="choosetoplaycenter">
          <div class="centermain" id="centermain">
              <div class="top">
                  <ul>
                      <li :class="{activebottomline:!isZhuiHao}" @click="setZhuiHao(false)">{{english.投注}}</li>
                      <li :class="{activebottomline:isZhuiHao}" @click="setZhuiHao(true)">{{english.追号}}</li>
                  </ul>
              </div>
              <!-- 猜双面 -->
              <div class="center">
                  <ul>
                      <li><span></span>&nbsp;&nbsp;{{activeName}}</li>
                      <li @click="declareModel = true">
                          <em></em>&nbsp;{{english.赔率说明}}</li>
                  </ul>
              </div>
              <div class="bigorsmall">
                  <div v-if="activeName==english.长龙投注" class="clUl">
                      <div v-for="(item,index) in playMethod" :key="index">
                        <div class="clTitle"><span>{{english[item.way]}}</span><span>{{english.期}} {{item.num}}</span></div>
                        <ul>
                          <li v-for="(item1,index1) in item.data" :key="index1" class="chooseSytle" @click="checkOdds(index1,item1.way,index)" v-bind:class='{checkedStyle:checkedOddsStyle(index1,index)}'>
                            <P>{{item1.way.indexOf('_') >= '0' ? item1.way.split('_')[1]: item1.way}}</P>
                            <p>{{item1.odds}}</p>
                          </li>
                        </ul>
                      </div>
                  </div>
                  <!-- 猜双面 -->
                  <template v-if="activeName == english.猜双面">
                    <div v-for="(item,index) in playMethod" :key="index" class="pmPanel">
                      <div class="pmTitle" :id="'pmTitle'+index">{{english[wanfOdds[chekedMethod-1].set[index]]}}</div>
                      <ul>
                        <li v-for="(item1,index1) in item" v-show="item1.title.split('_')[1]!='质'&&item1.title.split('_')[1]!='合'" :key="index1" class="chooseSytle" @click="checkOdds(index1,item1.title,index)" v-bind:class='{checkedStyle:checkedOddsStyle(index1,index)}'>
                          <p>{{english[item1.title.split('_')[1]]}}</p>
                          <p>{{item1.value}}</p>
                        </li>
                      </ul>
                    </div>
                  </template>
                  <template v-if="activeName == english.猜数字">
                    <div v-for="(item,index) in playMethod" :key="index" class="pmPanel">
                      <div class="pmTitle" :id="'pmTitle'+index">{{english[wanfOdds[chekedMethod-1].set[index]]}}</div>
                      <div class="guessNum">
                        <p v-for="(item1,index1) in item" :key="index1" class="chooseSytle" @click="checkOdds(index1,item1.title,index)" v-bind:class='{checkedStyle:checkedOddsStyle(index1,index)}'>
                          <span>{{item1.title.split('_')[1]}}</span>
                          <span>{{item1.value}}</span>
                        </p>
                      </div>
                    </div>
                  </template>
                  <template v-if="activeName == english.猜总和">
                    <ul>
                      <li v-for="(item,index) in playMethod" :key="index" class="chooseSytle" @click="checkOdds(index,item.title)" v-bind:class='{checkedStyle:checkedOddsStyle(index)}'>
                        <p>{{english[item.title.split('_')[1]]}}</p>
                        <p>{{item.value}}</p>
                      </li>
                    </ul>
                  </template>
              </div>
          </div>
          <!-- 投注操作区 -->
          <div class="choosetoplayfooter" id="choosetoplayfooter" v-show="!isZhuiHao">
              <div class="havebet">
                  <p>{{english.已选}}&nbsp;
                      <span>{{oddsWay.length}}</span>&nbsp;{{english.注}}，{{english.共}}&nbsp;
                      <span>{{totalMoney}}</span>&nbsp;{{english.元宝}}</p>
              </div>
              <div class="inputbet">
                  <InputNumber @on-change="checkedMoneyIdx=null" :min='1' :formatter="(value)=> `${Math.round(value)}`" :parser="(value)=> `${Math.round(value)}`" type="text" :placeholder="english.请输入下注金额" v-model="checkedMoney" style="width:200px !important"></InputNumber>
                  <span @click="minBetAdd()">{{english.最小投注}}</span>
              </div>
              <div class="bet">
                  <ul>
                      <li v-for="(item,index) in moneyList" @click="checkMoney(index,item)" :key="index" v-bind:class='{ckMoneyStyle:index==checkedMoneyIdx}'>{{item>=1000 ? Number(item)/1000 + 'K' : item}}</li>
                      <li @click="checkMoney(100, english.梭哈)" v-bind:class='{ckMoneyStyle:checkedMoneyIdx==100}'>{{english.梭哈}}</li>
                  </ul>
              </div>
              <div class="reset">
                  <Button type="primary" @click="openOddsDlg()" :disabled="oddsWay.length<1" v-bind:class='{disabledBtn:oddsWay.length<1}' class="qrBetBtn">{{english.确认下注}}</Button>
                  <Button type="primary" @click="resetOdds()">{{english.重置}}</Button>
              </div>
          </div>
          <!-- 追号操作区 -->
          <div class="zhPanel" id="zhPanel" v-show="isZhuiHao">
            <div class="zfInput">
              {{english.期}} <InputNumber  type="text" :max="100" :min="1" placeholder="" v-model="manyQi" style=""></InputNumber>&nbsp;&nbsp;
              {{english.翻}}<InputNumber type="text" :min="1" placeholder="" v-model="manyBei" style=""></InputNumber>{{english.倍}}
            </div>
            <InputNumber type="text" :placeholder="english.请输入下注金额" class="moneyInput" v-model="checkedMoney"></InputNumber>                
            <div class="btnBox">
              <Button type="primary" @click="setZhDlg(true)">{{english.生成追号}}</Button>
            </div>
          </div>
          <!-- 点击收缩左边的两栏 -->
          <em class="shrink" @click="showLeftBarFun" v-show="showLeftBar"></em>
      </div>
      </div>
      <Modal
        v-model="sendOddsDlg"
        class-name=""
        :closable="false"
        :scrollable="true"
        :mask-closable="false">
        <p slot="header">{{english.期}} {{timeMessages.issue}}, {{english.下注清单}}</p>
        <Table height="300" :columns="columns1" :data="oddsTableData" style="font-size:14px;" v-if="oddsTableData.length>0"></Table>     
        <div class="tableNoData" v-else>
          <img src="@/assets/images/tableNoData.png" alt="">
        </div>
        <div slot="footer">
          <p class="tableTip">{{english.共}} <span>{{oddsWay.length}}</span>{{english.注}}, {{english.金额}}<span>{{totalMoney}}</span><s></s></p>
          <Button @click="closeOddsDlg">{{english.取消}}</Button>            
          <Button type="primary" @click="sendOdds" :disabled="oddsTableData.length<1||stopBet" :loading="sendFlag">{{english.确认投注}}</Button>
        </div>
      </Modal>
      <Modal
        v-model="showZhDlg"
        class-name=""
        :title="english.追号清单"
        width="600"
        :scrollable="true"
        :mask-closable="false">
        <div class="zhPanel2">
          {{english.期}} <InputNumber type="text" :max="100" :min="1" placeholder="" v-model="manyQi" style=""></InputNumber>&nbsp;&nbsp;
          {{english.翻}}<InputNumber type="text" :min="1" placeholder="" v-model="manyBei" style=""></InputNumber>{{english.倍}}
          <InputNumber type="text" :placeholder="english.请输入下注金额" class="moneyInput2" v-model="checkedMoney"></InputNumber>          
          <Button type="primary" @click="setZhDlg(true)">{{english.生成追号}}</Button>
        </div>
        <Table height="300" :columns="columns2" :data="zhTableData" style="font-size:14px;"></Table>
        <p class="zjjtP"><Checkbox v-model="zhType">{{english.中奖即停}}</Checkbox></p>
        <div slot="footer">
          <p class="tableTip">{{english.共}}<span>{{manyQiNow}}</span>{{english.注}}, {{english.金额}}<span>{{totalZhMoney}}</span><s></s></p>
          <Button @click="setZhDlg(false)">{{english.取消}}</Button>            
          <Button type="primary" @click="confromZh()" :disabled="zhTableData.length<1||stopBet">{{english.确认投注}}</Button>
        </div>
      </Modal>
      <Modal
          v-model="declareModel"
          :title="english.赔率说明"
          @on-ok="declareModel = false"
          :scrollable="true"
          cancel-text
          :ok-text="english.关闭"
          width='600'
          >
          <p style="max-height: 500px;overflow-y: auto;">
            <pre v-html="RoomDateList"></pre>
          </p>
          
      </Modal>
  </div>
</template>

<script>
//aa
import { websocketsend } from "@/common/js/websocket";
import _ from "lodash";
import removeImg from "@/assets/images/remove.png";
import { mapGetters, mapActions } from "vuex";
import { timeFormat } from "@/assets/js/dom"
import english from "@/assets/json/English.json";

import router from "@/router";
export default {
  props: [
    "oddsData",
    "timeMessages",
    "cRoomData",
    "BetmoneyList",
    "bindAbsClass",
    "lDragonData"
  ],
  data() {
    return {
      english: english,
      sendFlag: false,
      playMethod: null,
      issue: "", //当前期号
      checkedOdd: [], //赔率
      moneyList: [5, 10, 50, 100, 500], //下注金额
      checkedMoney: 1, //金额
      checkOddData: false, //提交投注校验
      checkedMoneyIdx: 0,
      checkedOddIndex: 0,
      oddsWay: [], //投注数组
      moneyArr: [], //金额数组
      totalMoney: 0,
      isZhuiHao: false, //是否是追号
      showZhDlg: false, //追号面板
      zhTableData: [], //追号tabledata
      zhBetData: [], //追号data
      zhType: true, //中奖即停
      manyQi: 1, //追几期
      manyQiNow: 1,
      manyBei: 1, //翻几倍
      totalZhMoney: 0, //追号总金额
      sendOddsDlg: false,
      chekedMethod: 1, //选中的玩法
      methodsArr: [english.猜双面, english.猜数字, english.猜总和],
      gymodel: false, //冠亚和未单独的model
      columns1: [
        //投注表格内容自定义
        {
          title: english.玩法,
          key: "name",
          align: "center"
        },
        {
          title: english.内容,
          key: "type",
          align: "center"
        },
        {
          title: english.金额,
          key: "money",
          align: "center",
          render: (h, params) => {
            return h("InputNumber", {
              props: {
                value: this.moneyArr[params.index],
                formatter: value => `${Math.round(value)}`,
                parser: value => `${Math.round(value)}`,
                max: 10000,
                min: 1
              },
              style: { width: "80px", textAlign: "center" },
              on: {
                input: e => {
                  this.totalMoney = 0;
                  this.$set(this.moneyArr, params.index, e);
                  for (var i = 0; i < this.moneyArr.length; i++) {
                    this.totalMoney += this.moneyArr[i];
                  }
                }
              }
            });
          }
        },
        {
          title: english.撤单,
          key: "remove",
          align: "center",
          render: (h, params) => {
            return h("img", {
              attrs: {
                src: removeImg
              },
              style: { cursor: "pointer", "vertical-align": "middle" },
              on: {
                click: () => {
                  if (this.chekedMethod == 5) {
                    this.totalMoney =
                      this.totalMoney - this.moneyArr[params.index];
                    this.oddsTableData.splice(params.index, 1);
                    this.moneyArr.splice(params.index, 1);
                    this.oddsWay = [];
                    this.checkedOdd = [];
                    this.guessGY = [];
                  } else {
                    this.totalMoney =
                      this.totalMoney - this.moneyArr[params.index];
                    this.oddsTableData.splice(params.index, 1);
                    this.moneyArr.splice(params.index, 1);
                    this.oddsWay.splice(params.index, 1);
                    this.checkedOdd.splice(params.index, 1);
                  }
                }
              }
            });
          }
        }
      ],
      columns2: [
        {
          title: english.期号,
          key: "issue",
          align: "center"
        },
        {
          title: english.投注金额,
          key: "money",
          align: "center"
        },
        {
          title: english.翻倍,
          key: "multiple",
          align: "center"
        }
      ],
      oddsTableData: [],
      wanfOdds: [
        //玩法菜单数据组织
        {
          name: english.猜双面,
          set: ["第一球", "第二球", "第三球", "第四球", "第五球"],
          panel: []
        },
        {
          name: english.猜数字,
          set: ["第一球", "第二球", "第三球", "第四球", "第五球"],
          panel: []
        },
        {
          name: english.猜总和,
          panel: []
        }
      ],
      renderGYH_gy: null, //猜亚和数据盒子
      renderGYH_num: null, //猜亚和数据盒子
      renderGYH_zd: null, //猜亚和数据盒子
      guessGY: [], //猜亚冠数据组织盒子
      declareModel: false, //
      RoomDateList: null, //房间信息
      roomId: null, //房间id
      lottery_type: null, // 彩种类型
      betLower: 1,
      showLeftBar: true,
      activeName: english.猜双面,
      token: localStorage.getItem('token')
    };
  },
  created() {
    this.roomId = this.$route.query.id
    this.lottery_type = this.$route.query.lottery_type;
    //   if(this.lottery_type == 4){
    //     this.wanfOdds[1].name = '猜号码'
    //     this.methodsArr[1] = '猜号码'
    //   }
  },
  mounted() {},
  computed: {
    ...mapGetters(["accountData","serTime","userInfo", "stopBet"])
  },

  methods: {
    showLeftBarFun: function() {
      this.showLeftBar = !this.showLeftBar;
      setTimeout(() => {
        var chatcontent = document.getElementById("chatcontent");
        var sendmsg = document.getElementById("sendmsg");
        sendmsg.style.width = chatcontent.offsetWidth + "px";
      });
    },
    changePlay(names) {
      this.activeName=names
      let f1Room = this.wanfOdds;
      for(var i=0;i<f1Room.length;i++){
        if(!_.isEmpty(f1Room[i].panel)&&f1Room[i].name==names){
          this.playMethod = f1Room[i].panel
          this.chekedMethod=i+1
        }
      }
      this.resetOdds()
    },
    setZhuiHao(flag) {
      //打开或关闭追号面板
      this.resetOdds();
      this.isZhuiHao = flag;
    },
    checkedOddsStyle: function(index, index1) {
      //选中style
      var newStr = index1+'_'+index
      if(this.isZhuiHao==true){
          if(this.checkedOddIndex == newStr){
            return true;
          }
        }else{
          for(var i=0;i<this.checkedOdd.length;i++){
            if(newStr==this.checkedOdd[i]){
              return true;
            }
          }
        }
    },
    setZhDlg: function(flag) {
      //设置追号数据
      if (flag) {
        if (this.checkedOddIndex == -1) {
          this.$Message.info(english.请选择玩法);
          return;
        }
        var issue = this.issue;
        var zhTableData = [];
        var zhBetData = [];
        var totalZhMoney = 0;
        this.manyQi = Math.round(this.manyQi);
        this.manyQiNow = this.manyQi;
        if (this.manyQi > 100) {
          this.$Message.info(english.最多追加100期+"!");
          this.manyQi = 100;
          return;
        }
        this.manyBei = Math.round(this.manyBei);
        this.checkedMoney = Math.round(this.checkedMoney);
        for (var i = 1; i <= this.manyQi; i++) {
          totalZhMoney += this.checkedMoney * Math.pow(this.manyBei, i - 1);
          zhTableData.push({
            issue: issue,
            money: this.checkedMoney * Math.pow(this.manyBei, i - 1) + english.元宝,
            multiple: Math.pow(this.manyBei, i - 1) + english.倍
          });
          var arr2 = this.checkedOddIndex.split("_");
          if (this.activeName==english.长龙投注){
            zhBetData.push({
              qihao: issue,
              money: this.checkedMoney * Math.pow(this.manyBei, i - 1),
              single_money: this.checkedMoney,
              way: this.playMethod[arr2[0]].data[arr2[1]].way,
              multiple: Math.pow(this.manyBei, i - 1)
            });
          }else{
            if(_.isArray(this.playMethod[0])){
              zhBetData.push({
                qihao: issue,
                money: this.checkedMoney * Math.pow(this.manyBei, i - 1),
                single_money: this.checkedMoney,
                way: this.playMethod[arr2[0]][arr2[1]].title,
                multiple: Math.pow(this.manyBei, i - 1)
              });
            }else{
              zhBetData.push({
                qihao: issue,
                money: this.checkedMoney * Math.pow(this.manyBei, i - 1),
                single_money: this.checkedMoney,
                way: this.playMethod[arr2[1]].title,
                multiple: Math.pow(this.manyBei, i - 1)
              });
            }
            
          }
          issue++;
        }
        this.totalZhMoney = totalZhMoney;
        this.zhTableData = zhTableData;
        this.zhBetData = zhBetData;
      }
      this.showZhDlg = flag;
    },
    confromZh: function() {
      //追号确认
      this.$Modal.confirm({
        content:
          "<p>"+english.本期追号投注+": " +
          english.期 + " " +this.manyQiNow +
          ", "+ english.共 + " " +
          this.totalZhMoney +
          english.元宝 + "</p>",
        scrollable: true,
        loading: true,
        onOk: () => {
          const agentData = {
            token: this.token,
            commandid: "3019",
            nickname: JSON.parse(localStorage.getItem("userData")).nickname,
            data: this.zhBetData,
            win_stop: this.zhType ? 1 : 0,
            avatar: JSON.parse(localStorage.getItem("userData")).avatar
          };
          websocketsend(agentData);
          this.showZhDlg = false;
        }
      });
    },
    checkOdds(index, title, index1) {
      //选中操作
      var newStr = index1+'_'+index;
      this.checkedOddIndex = newStr; 
        if (this.checkedOdd.indexOf(newStr) == -1) {
          this.checkedOdd.push(newStr);
          this.oddsWay.push(title);
          this.totalMoney = this.oddsWay.length * this.checkedMoney;
        } else {
          for (var i = 0; i < this.checkedOdd.length; i++) {
            if (this.checkedOdd[i] == newStr) {
              this.checkedOdd.splice(i, 1);
              this.oddsWay.splice(i, 1);
              this.totalMoney = this.oddsWay.length * this.checkedMoney;
            }
          }
        }
      
    },

    checkMoney(index, item) {
      if (item == english.梭哈) {
        let accountData = this.accountData.money_usable;
        this.checkedMoney = Math.round(Number(accountData));
        this.checkedMoneyIdx = index;
        this.totalMoney = this.checkedMoney * this.checkedOdd.length;
      } else {
        this.checkedMoney = item;
        this.checkedMoneyIdx = index;
        this.totalMoney = this.checkedMoney * this.checkedOdd.length;
      }
    },
    resetOdds() {
      this.zhBetData = [];
      this.zhTableData = [];
      this.manyBei = 1;
      this.manyQi = 1;
      this.checkedOddIndex = -1;
      this.checkedOdd = [];
      this.oddsWay = [];
      this.checkedMoney = this.moneyList[0];
      this.checkedMoneyIdx = 0;
      this.totalMoney = 0;
    },
    closeOddsDlg: function() {
      this.totalMoney = this.checkedMoney * this.oddsWay.length;
      this.sendOddsDlg = false;
    },
    openOddsDlg: function() {
      let newMoneyArr = [];
      let newOddsArr = [];
      for (var i = 0; i < this.oddsWay.length; i++) {
        newOddsArr.push({
          name: this.activeName,
          type: this.oddsWay[i].split("_").length>1?english[this.oddsWay[i].split("_")[0]]+"_"+english[this.oddsWay[i].split("_")[1]]:english[this.oddsWay[i]],
          money: this.checkedMoney,
          remove: english.删除
        });
        newMoneyArr.push(this.checkedMoney);
      }
      this.moneyArr = newMoneyArr;
      this.oddsTableData = newOddsArr;
      this.sendOddsDlg = true;
    },
    sendOdds: function() {
      this.sendFlag=true
      setTimeout(()=>{
        this.sendFlag=false
      },1000)
      const agentData = {
        "token": this.token,
        commandid: "3006",
        nickname: JSON.parse(localStorage.getItem("userData")).nickname,
        way: this.oddsWay,
        money: this.moneyArr,
        avatar: JSON.parse(localStorage.getItem("userData")).avatar
      };
      var way = this.oddsWay
      var money = this.moneyArr
      var uniNo = new Date().getTime()+Math.floor(Math.random()*900)+100
      var params = {
        user_id: this.userInfo.id,
        lottery_type: this.$route.query.lottery_type,
        room_id: this.$route.query.id,
        nickname: this.userInfo.nickname,
        money: JSON.stringify(money),
        issue: this.issue,
        way: JSON.stringify(way),
        serTime: this.serTime,
        uniNo: uniNo,
        token: this.token,
      }
      let lotRstData={
        isHttp: true,
        isLoading: true,
        avatar: this.userInfo.avatar,
        commandid: 30007,
        issue: this.issue,
        money: money,
        nickname: this.userInfo.nickname,
        time: timeFormat(new Date().getTime()),
        total_money: this.totalMoney,
        total_zushu: this.checkedOdd.length,
        uid: this.userInfo.id,
        uniNo: uniNo,
        sort: this.userInfo.honor.sort,
        serTime: this.serTime,
        order_no: '',
        way: way,
        loadingFailed: false,
        failedText: '',
      }
      this.$store.commit('setLotRstData', lotRstData)
      this.$http.post(this.urlRequest + "?m=api&c=workerman&a=http_betting", params).then(result=>{
        result=result.data
        if(result.commandid!=3007){
          var failedObj = JSON.parse(JSON.stringify(lotRstData))
          failedObj.loadingFailed=true
          failedObj.failedText=result.content
          this.$store.commit('setNewRstData', failedObj)
          return
        }
        var accountData = this.accountData
        accountData.money_usable=result.current_money
        this.$store.commit('setAccountData', accountData)
        this.$store.commit('setNewRstData', result)
      },
      err=>{
        lotRstData.loadingFailed=true
        lotRstData.failedText=english.投注超时+', '+english.请重新下注
        this.$store.commit('setNewRstData', lotRstData)
      })
      // websocketsend(agentData);
      this.sendOddsDlg = false;
    },
    getodds: function(data) {
      var index = data.split('_')[0]
      var name = data.split('_')[1]
      if(name!=this.activeName){
        this.changePlay(name)
      }
      setTimeout(()=>{
        let anchorElement = document.getElementById('pmTitle'+index)
        var centermain = document.getElementById('centermain')
        centermain.scrollTop= anchorElement.offsetTop
      },20)
    },
    navClick: function(name){
      if(name!=this.activeName){
        this.changePlay(name)
      }
    },
    selectNum(num) {
      let result = "gusessNum-" + num;
      return result;
    },
    minBetAdd() {
      // 最小投注的事件
      this.checkedMoney = Number(this.betLower);
      this.totalMoney = this.checkedMoney * this.checkedOdd.length;
      this.checkedMoneyIdx = null;
    }
  },
  components: {},
  watch: {
    oddsData: function(oldVal, newVal) {
      let f1Room = oldVal;
      let that = this;
      if (!_.isEmpty(f1Room)) {
        for (var i = 0; i < that.wanfOdds.length; i++) {
          if (!_.isEmpty(f1Room.panel_1) && that.wanfOdds[i].name == english.猜双面) {
            var list = that.wanfOdds[i].set
            var value = []
            for(var k=0;k<list.length;k++){
              value.push(f1Room['panel_1'][list[k]])
            }
            that.wanfOdds[i].panel = value;
          } else if (
            !_.isEmpty(f1Room.panel_2) &&
            that.wanfOdds[i].name == english.猜数字
          ) {
            var list = that.wanfOdds[i].set
            var value = []
            for(var k=0;k<list.length;k++){
              value.push(f1Room['panel_2'][list[k]])
            }
            that.wanfOdds[i].panel = value;
          } else if (
            !_.isEmpty(f1Room.panel_3) &&
            that.wanfOdds[i].name == english.猜总和
          ) {
            that.wanfOdds[i].panel = f1Room.panel_3;
          }
          if (_.isEmpty(that.wanfOdds[i].panel)) {
            that.wanfOdds.splice(i, 1);
            i--;
          }
        }
      }
      // console.log(that.wanfOdds,'f1Rooms')
      if (this.playMethod == undefined) {
        this.playMethod=that.wanfOdds[0].panel
        return
        let f1Room = that.wanfOdds;
        if (f1Room[0].name == english.猜双面) {
          for (var p in f1Room[0].panel) {
            if (p == english.第一球) {
              this.playMethod = f1Room[0].panel[p];
            }
          }
        }
      }
    },
    lDragonData: function(newVal, oldVal){
      if(newVal){
        for(var i=0;i<this.wanfOdds.length;i++){
          if(this.wanfOdds[i].name==english.长龙投注){
            this.wanfOdds.splice(i,1)
            this.methodsArr.splice(i,1)
          }
        }
        if(newVal.length>0){
          this.wanfOdds.push({
            name:english.长龙投注,
            panel:newVal
          })
          this.methodsArr.push(english.长龙投注)
          if(this.activeName==english.长龙投注){
            this.playMethod=newVal
          }
        }
      }
    },
    timeMessages: function(oldVal, newVal) {
      this.issue = oldVal.issue;
    },
    cRoomData: function(oldVal, newVal) {
      let dataArr = oldVal.p_room;
      for (var i = 0; i < dataArr.length; i++) {
        if (this.roomId == dataArr[i].id||this.roomId == dataArr[i].idd) {
          this.betLower = dataArr[i].lower;
          if (dataArr[i].odds_exp == undefined || dataArr[i].odds_exp == "") {
            this.RoomDateList = english.暂无数据;
          } else {
            this.RoomDateList = dataArr[i].odds_exp;
          }
        }
      }
    },
    BetmoneyList: function(oldVal, newVal) {
      this.moneyList = oldVal;
      this.checkedMoney = this.moneyList[0];
    },
    checkedMoney: function(newVal, oldVal) {
      this.totalMoney = this.checkedOdd.length * newVal;
    }
  },
  deactivated() {},
  beforeDestroy() {}
};
</script>

