<template>
  
  <div>
    <i @click="showLeftBarFun" v-show="!showLeftBar"></i>
    <div class="leftBar" v-show="showLeftBar">
      <div class="choosetoplayleft">
          <div class="choosetheplay">{{english.选择玩法}}</div>
          <ul class="playList">
            <li v-for="(item,index) in wanfOdds" :key="index"  :class="{'choosedactive':chekedMethod==index}">
              <Dropdown placement="right-start" v-if="item.set" @on-click="getodds">
                <a href="javascript:void(0)">
                    {{item.name}}
                </a>
                <DropdownMenu slot="list">
                    <DropdownItem v-for="(itemChild,index) in item.set" :name="item.name+ '--' +itemChild+'|'+index" :key="index">{{itemChild}}</DropdownItem>
                </DropdownMenu>
              </Dropdown>
              <span class="otherf1" @click="changePlay(index)" v-else>
                {{item.name}}
              </span>
            </li>
              <!-- 选择玩法鼠标移入时显示的排名玩法列表 -->
              <div class="sort" style="display:none">
                  <ul>
                      <li>{{english.第一名}}</li>
                      <li>{{english.第二名}}</li>
                      <li>{{english.第三名}}</li>
                      <li>{{english.第四名}}</li>
                      <li>{{english.第五名}}</li>
                  </ul>
              </div>
          </ul>
        </div>
        <div class="choosetoplaycenter">
            <div class="centermain" id="centermain">
                <div class="top">
                    <ul>
                        <li :class="{activebottomline:!isZhuiHao}" @click="setZhuiHao(false)">{{english.投注}}</li>
                        <li :class="{activebottomline:isZhuiHao}" @click="setZhuiHao(true)">{{english.追号}}</li>
                    </ul>
                </div>
                <div class="center">
                    <ul>
                        <li>
                            <span></span>&nbsp;&nbsp;&nbsp;{{methodsArr[chekedMethod]}}</li>
                        <li @click="declareModel = true">
                            <em></em>&nbsp;{{english.赔率说明}}</li>
                    </ul>
                </div>
                <div class="bigorsmall">
                    <ul>
                      <template v-if="playMethod.data && (chekedMethod==0||chekedMethod==1||chekedMethod==2)">
                        <div class="cszPanel">
                          <div v-for="(item,index) in playMethod.data.num" class="cszStyle" @click="checkOdds(index,item.title,0)" v-bind:class='{checkedStyle:checkedOddsStyle(index,0)}'>
                            <div :class="`cszBox${item.title.split('_')[1]}`" class="cszBox">{{item.title.split('_')[1]}}</div>
                            <p>{{item.value}}</p>
                          </div>
                        </div>
                        <li v-for="(item,index) in playMethod.data.text" class="chooseSytle" @click="checkOdds(index,item.title,1)" v-bind:class='{checkedStyle:checkedOddsStyle(index,1)}'>
                          <p>{{item.title.split('_')[1]}}</p>
                          <p>{{item.value}}</p>
                        </li>
                      </template>
                      <div class="cszPanel" v-else-if="playMethod.data && chekedMethod==3">
                        <!-- 连码 -->
                        <div v-if="chekedMethod==3" v-for="(item,index) in playMethod.data.num" class="cszStyle" @click="checkLhcOdds(index,item.title)" v-bind:class='{checkedStyle:checkedOddsStyle(index)}'>
                          <div :class="`cszBox${item.title.split('_')[1]}`" class="cszBox">{{item.title.split('_')[1]}}</div>
                          <p>{{item.value}}</p>
                        </div>
                      </div>
                      <div class="bbPanel" v-else-if="playMethod.data && (chekedMethod==4||chekedMethod==5||chekedMethod==6||chekedMethod==7)">
                        <div v-for="(item,index) in playMethod.data.text" @click="checkOdds(index,item.title)" v-bind:class='{checkedStyle:checkedOddsStyle(index)}'>
                          <div>
                            {{item.title.split('_')[1]}}
                            <span :class="{bbPeiLv:(chekedMethod==4)}" class="wsPeiLv">x{{item.value}}</span>
                          </div>
                          <div>
                            <span v-for="item1 in item.rest" class="cszBox" :class="`cszBox${item1}`">{{item1}}</span>
                          </div>
                        </div>
                      </div>
                      <div class="bbPanel" v-else-if="playMethod.data && (chekedMethod==8||chekedMethod==9)">
                        <!-- 连肖 -->
                        <div v-for="(item,index) in playMethod.data.text" @click="checkLhcOdds(index,item.title)" v-bind:class='{checkedStyle:checkedOddsStyle(index)}'>
                          <div>
                            {{item.title.split('_')[1]}}
                            <span class="wsPeiLv">x{{item.value}}</span>
                          </div>
                          <div>
                            <span v-for="item1 in item.rest" class="cszBox" :class="`cszBox${item1}`">{{item1}}</span>
                          </div>
                        </div>
                      </div>
                      <div class="cszPanel" v-else-if="playMethod.data && chekedMethod==10">
                        <!-- 不中 -->
                        <div v-for="(item,index) in playMethod.data.text" class="cszStyle" @click="checkLhcOdds(index,item.title)" v-bind:class='{checkedStyle:checkedOddsStyle(index)}'>
                          <div :class="`cszBox${item.title.split('_')[1]}`" class="cszBox">{{item.title.split('_')[1]}}</div>
                          <p>{{item.value}}</p>
                        </div>
                      </div>
                      <div v-else-if="playMethod && chekedMethod==11||chekedMethod==12" v-for="(item,index) in playMethod" class="zmPanel">
                        <div class="zmTitle">{{item.name}}</div>
                        <div class="cszPanel">
                          <li v-for="(item1,index1) in item.data.text" class="chooseSytle" @click="checkOdds(index1,item1.title,index)" v-bind:class='{checkedStyle:checkedOddsStyle(index1,index)}'>
                            <p>{{item1.title.split('_')[1]}}</p>
                            <p>{{item1.value}}</p>
                          </li>
                        </div>
                      </div>
                      <div v-else-if="playMethod && chekedMethod==13" v-for="(item,index) in playMethod" class="zmPanel">
                        <div class="clTitle"><span>{{item.way}}</span> {{english.期}} <span>{{item.num}}</span></div>
                        <ul>
                          <li v-for="(item1,index1) in item.data" :key="index1" class="chooseSytle" @click="checkOdds(index1,item1.way,index)" v-bind:class='{checkedStyle:checkedOddsStyle(index1,index)}'>
                            <P>{{item1.way.indexOf('_') >= '0' ? item1.way.split('_')[1]: item1.way}}</P>
                            <p>{{item1.odds}}</p>
                          </li>
                        </ul>
                      </div>
                    </ul>
                </div>
            </div>
            <!-- 投注操作区 -->
            <div class="choosetoplayfooter" id="choosetoplayfooter" v-show="!isZhuiHao">
                <div class="havebet">
                    <p>{{english.已选}}&nbsp;
                        <span>{{realCkedOdd.length}}</span>&nbsp;{{english.注}}, {{english.共}}&nbsp;
                        <span>{{totalMoney}}</span>&nbsp;{{english.元宝}}</p>
                </div>
                <div class="inputbet">
                    <InputNumber @on-change="checkedMoneyIdx=null" :max="999999999" :min='1' :formatter="(value)=> `${Math.round(value)}`" :parser="(value)=> `${Math.round(value)}`" type="text" :placeholder="english.请输入下注金额" v-model="checkedMoney" style="width:200px !important"></InputNumber>
                    <span @click="minBetAdd()">{{english.最小投注}}</span>
                </div>
                <div class="bet">
                    <ul>
                        <li v-for="(item,index) in moneyList" @click="checkMoney(index,item)" :key="index" v-bind:class='{ckMoneyStyle:index==checkedMoneyIdx}'>{{item>=1000 ? Number(item)/1000 + 'K' : item}}</li>
                        <li @click="checkMoney(100,english.梭哈)" v-bind:class='{ckMoneyStyle:checkedMoneyIdx==100}'>{{english.梭哈}}</li>
                    </ul>
                </div>
                <div class="reset">
                    <Button type="primary" @click="openOddsDlg()" :disabled="realCkedOdd.length<1" class="qrBetBtn">{{english.确认下注}}</Button>
                    <Button type="primary" @click="resetOdds()">{{english.重置}}</Button>
                </div>
            </div>
            <!-- 追号操作区 -->
            <div class="zhPanel" id="zhPanel" v-show="isZhuiHao">
              <div class="zfInput">
                {{english.期}} <InputNumber type="text" :max="100" :min="1" placeholder="" v-model="manyQi" style=""></InputNumber>&nbsp;&nbsp;
                {{english.翻}}<InputNumber type="text" :min="1" placeholder="" v-model="manyBei" style=""></InputNumber>{{english.倍}}
              </div>
              <InputNumber type="text" :placeholder="english.请输入下注金额" class="moneyInput" v-model="checkedMoney"></InputNumber>                
              <div class="btnBox">
                <Button type="primary" @click="setZhDlg(true)" :disabled="realCkedOdd.length<1">{{english.生成追号}}</Button>
              </div>
            </div>
            <!-- 点击收缩左边的两栏 -->
            <em class="shrink" @click="showLeftBarFun" v-show="showLeftBar"></em>
        </div>
        <Modal
          v-model="sendOddsDlg"
          class-name=""
          :closable="false"
          :scrollable="true"
          :mask-closable="false">
          <p slot="header">{{english.期}} {{issue}}, {{english.下注清单}}</p>
          <Table height="300" :columns="columns1" :data="oddsTableData" style="font-size:14px;" v-if="oddsTableData.length>0"></Table>
          <div class="tableNoData" v-else>
            <img src="@/assets/images/tableNoData.png" alt="">
          </div>
          <div slot="footer">
            <p class="tableTip">{{english.共}} <span>{{realCkedOdd.length}}</span>{{english.注}}, {{english.金额}}<span>{{totalMoney}}</span><s></s></p>
            <Button @click="closeOddsDlg">{{english.取消}}</Button>            
            <Button type="primary" @click="sendOdds" :disabled="oddsTableData.length<1||stopBet" :loading="sendFlag">{{english.确认投注}}</Button>
          </div>
        </Modal>
        <Modal
          v-model="showZhDlg"
          class-name=""
          :title="english.追号清单"
          width="600"
          :scrollable="true"
          :mask-closable="false">
          <div class="zhPanel2">
            {{english.期}} <InputNumber type="text" :max="100" :min="1" placeholder="" v-model="manyQi" style=""></InputNumber>&nbsp;&nbsp;
            {{english.翻}}<InputNumber type="text" :min="1" placeholder="" v-model="manyBei" style=""></InputNumber>{{english.倍}}
            <InputNumber type="text" :placeholder="english.请输入下注金额" class="moneyInput2" v-model="checkedMoney"></InputNumber>          
            <Button type="primary" @click="setZhDlg(true)" :disabled="realCkedOdd.length<1">{{english.生成追号}}</Button>
          </div>
          <Table height="300" :columns="columns2" :data="zhTableData" style="font-size:14px;"></Table>
          <p class="zjjtP"><Checkbox v-model="zhType">{{english.中奖即停}}</Checkbox></p>
          <div slot="footer">
            <p class="tableTip">{{english.共}} <span>{{manyQiNow}}</span>{{english.注}}, {{english.金额}}<span>{{totalZhMoney}}</span></p>
            <Button @click="setZhDlg(false)">{{english.取消}}</Button>            
            <Button type="primary" @click="confromZh()" :disabled="zhTableData.length<1||stopBet">{{english.确认投注}}</Button>
          </div>
        </Modal>
        <Modal
            v-model="declareModel"
            :title="english.赔率说明"
            @on-ok="declareModel = false"
            :scrollable="true"
            cancel-text
            :ok-text="english.关闭"
            width='600'
            >
            <p style="max-height: 500px;overflow-y: auto;">
              <pre v-html="RoomDateList"></pre>
            </p>
        </Modal>
    </div>
  </div>
</template>

<script>
import { websocketsend } from "@/common/js/websocket";
import _ from "lodash";
import removeImg from "@/assets/images/remove.png";
import { mapGetters, mapActions } from "vuex";
import { timeFormat } from "@/assets/js/dom";
import english from "@/assets/json/English.json";
export default {
  props: [
    "oddsData",
    "timeMessages",
    "cRoomData",
    "BetmoneyList",
    "bindAbsClass",
    "lDragonData"
  ],
  data() {
    return {
      english: english,
      sendFlag: false,
      playMethod: [],
      issue: "", //当前期号
      checkedOdd: [], //选中的玩法数组
      realCkedOdd: [], //真正的注数数组
      lmTitleArr: [], //连码title拼接
      moneyList: [5, 10, 50, 100, 500], //下注金额
      checkedMoney: 1, //金额
      checkedMoneyIdx: 0,
      checkedOddIndex: 0,
      oddsWay: [], //投注数组
      moneyArr: [], //金额数组
      totalMoney: 0,
      isZhuiHao: false, //是否是追号
      showZhDlg: false, //追号面板
      zhTableData: [], //追号tabledata
      zhBetData: [], //追号data
      zhType: true, //中奖即停
      manyQi: 1, //追几期
      manyQiNow: 1,
      manyBei: 1, //翻几倍
      totalZhMoney: 0, //追号总金额
      sendOddsDlg: false,
      chekedMethod: 0, //选中的玩法
      chekedMethod1: 0,
      columns1: [
        //投注表格内容自定义
        {
          title: english.玩法,
          key: "name",
          align: "center"
        },
        {
          title: english.内容,
          key: "type",
          align: "center"
        },
        {
          title: english.金额,
          key: "money",
          align: "center",
          render: (h, params) => {
            return h("InputNumber", {
              props: {
                value: this.moneyArr[params.index],
                formatter: value => `${Math.round(value)}`,
                parser: value => `${Math.round(value)}`,
                max: 10000,
                min: 1
              },
              style: { width: "80px", textAlign: "center" },
              on: {
                input: e => {
                  e = Math.round(e);
                  if (this.oddsTableData.length == 1) {
                    this.totalMoney = this.realCkedOdd.length * e;
                    this.$set(this.moneyArr, params.index, e);
                    this.checkedMoney = e;
                  } else {
                    this.totalMoney = 0;
                    this.$set(this.moneyArr, params.index, e);
                    for (var i = 0; i < this.moneyArr.length; i++) {
                      this.totalMoney += this.moneyArr[i];
                    }
                  }
                }
              }
            });
          }
        },
        {
          title: english.撤单,
          key: "remove",
          align: "center",
          render: (h, params) => {
            return h("img", {
              attrs: {
                src: removeImg
              },
              style: { cursor: "pointer", "vertical-align": "middle" },
              on: {
                click: () => {
                  this.totalMoney =
                    this.totalMoney - this.moneyArr[params.index];
                  this.oddsTableData.splice(params.index, 1);
                  this.moneyArr.splice(params.index, 1);
                  this.oddsWay.splice(params.index, 1);
                  this.checkedOdd.splice(params.index, 1);
                }
              }
            });
          }
        }
      ],
      columns2: [
        {
          title: english.期号,
          key: "issue",
          align: "center"
        },
        {
          title: english.投注金额,
          key: "money",
          align: "center"
        },
        {
          title: english.翻倍,
          key: "multiple",
          align: "center"
        }
      ],
      //红蓝绿波
      redBox: [1, 2, 7, 8, 12, 13, 18, 19, 23, 24, 29, 30, 34, 35, 40, 45, 46],
      blueBox: [3, 4, 9, 10, 14, 15, 20, 25, 26, 31, 36, 37, 41, 42, 47, 48],
      greenBox: [5, 6, 11, 16, 17, 21, 22, 27, 28, 32, 33, 38, 39, 43, 44, 49],
      methodsArr: [
        english.特码-特A,
        english.正码,
        english.正特,
        english.连码,
        english.半波,
        english.尾数,
        english.一肖,
        english.特肖,
        english.连肖,
        english.连尾,
        english.不中,
        english.正码6,
        english.正6龙虎
      ],
      wanfOdds: [
        //玩法菜单数据组织
        {
          name: english.特码,
          set: [english.特A, english.特B],
          panel: []
        },
        {
          name: english.正码,
          set: [english.正A, english.正B],
          panel: []
        },
        {
          name: english.正特,
          set: [english.正1特, english.正2特, english.正3特, english.正4特, english.正5特, english.正6特],
          panel: []
        },
        {
          name: english.连码,
          set: [english.三中二, english.三全中, english.二全中, english.二中特, english.特串],
          panel: []
        },
        {
          name: english.半波,
          panel: []
        },
        {
          name: english.尾数,
          panel: []
        },
        {
          name: english.一肖,
          panel: []
        },
        {
          name: english.特肖,
          panel: []
        },
        {
          name: english.连肖,
          set: [
            english.二肖连中,
            english.三肖连中,
            english.四肖连中,
            english.二肖连不中,
            english.三肖连不中,
            english.四肖连不中
          ],
          panel: []
        },
        {
          name: english.连尾,
          set: [
            english.二尾连中,
            english.三尾连中,
            english.四尾连中,
            english.二尾连不中,
            english.三尾连不中,
            english.四尾连不中
          ],
          panel: []
        },
        {
          name: english.不中,
          set: [english.五不中, english.六不中, english.七不中, english.八不中, english.九不中, english.十不中],
          panel: []
        },
        {
          name: english.正码6,
          panel: []
        },
        {
          name: english.正6龙虎,
          panel: []
        }
      ],
      oddsTableData: [],
      declareModel: false, //
      RoomDateList: null, //房间信息
      roomId: null, //房间id
      betLower: 1,
      showLeftBar: true,
      token: localStorage.getItem('token')
    };
  },
  created() {
    this.roomId = this.$route.query.id
  },
  mounted() {},
  computed: {
    ...mapGetters(["accountData","serTime","userInfo", "stopBet"])
  },

  methods: {
    showLeftBarFun: function() {
      this.showLeftBar = !this.showLeftBar;
      setTimeout(() => {
        var chatcontent = document.getElementById("chatcontent");
        var sendmsg = document.getElementById("sendmsg");
        sendmsg.style.width = chatcontent.offsetWidth + "px";
      });
    },
    getodds: function(names) {
      let nameArr = [];
      nameArr = names.split("--");
      let f1 = nameArr[0];
      let f2 = nameArr[1].split("|")[0];
      let f3 = nameArr[1].split("|")[1];
      this.checkedOdd = [];
      this.realCkedOdd = [];
      this.lmTitleArr = [];
      this.oddsWay = [];
      this.totalMoney = 0;
      for (var i = 0; i < this.methodsArr.length; i++) {
        if (this.methodsArr[i].indexOf(f1) > -1) {
          this.methodsArr[i] = f1 + "-" + f2;
          this.chekedMethod = i;
          this.chekedMethod1 = f3;
          this.playMethod = this.wanfOdds[this.chekedMethod].panel[f3];
          break;
        }
      }
    },
    changePlay(id) {
      this.chekedMethod = id;
      this.resetOdds();
      //换玩法
      if (id == 11 || id == 12 || id == 13) {
        this.playMethod = this.wanfOdds[id].panel;
        return;
      }
      this.playMethod = this.wanfOdds[id].panel[0];
    },
    setZhuiHao(flag) {
      //打开或关闭追号面板
      this.resetOdds();
      this.isZhuiHao = flag;
    },
    checkedOddsStyle: function(index, type) {
      //选中style
      if (type != undefined) {
        index = type + "_" + index;
      }
      if (this.isZhuiHao == true) {
        if (
          this.chekedMethod == 3 ||
          this.chekedMethod == 8 ||
          this.chekedMethod == 9 ||
          this.chekedMethod == 10
        ) {
          //连码
          if (
            this.checkedOdd.indexOf(index) != -1 &&
            this.realCkedOdd.length <= 1
          ) {
            return true;
          }
        } else if (this.checkedOddIndex == index) {
          return true;
        }
      } else {
        if (this.checkedOdd.indexOf(index) != -1) {
          return true;
        }
      }
    },
    setZhDlg: function(flag) {
      //设置追号数据
      if (flag) {
        if (this.realCkedOdd.length < 1) {
          this.$Message.info(english.请选择玩法);
          return;
        }
        var issue = this.issue;
        var zhTableData = [];
        var zhBetData = [];
        var totalZhMoney = 0;
        this.manyQi = Math.round(this.manyQi);
        this.manyQiNow = this.manyQi;
        if (this.manyQi > 100) {
          this.$Message.info(english.最多追加100期+"!");
          this.manyQi = 100;
          return;
        }
        this.manyBei = Math.round(this.manyBei);
        this.checkedMoney = Math.round(this.checkedMoney);
        for (var i = 1; i <= this.manyQi; i++) {
          totalZhMoney += this.checkedMoney * Math.pow(this.manyBei, i - 1);
          zhTableData.push({
            issue: issue,
            money: this.checkedMoney * Math.pow(this.manyBei, i - 1) + english.元宝,
            multiple: Math.pow(this.manyBei, i - 1) + english.倍
          });
          zhBetData.push({
            qihao: issue,
            money: this.checkedMoney * Math.pow(this.manyBei, i - 1),
            single_money: this.checkedMoney,
            way: this.oddsWay[0],
            multiple: Math.pow(this.manyBei, i - 1)
          });
          issue++;
        }
        this.totalZhMoney = totalZhMoney;
        this.zhTableData = zhTableData;
        this.zhBetData = zhBetData;
      }
      this.showZhDlg = flag;
    },
    confromZh: function() {
      //追号确认
      this.$Modal.confirm({
        content:
          "<p>"+english.本期追号投注+":" +
          english.期+" "+this.manyQiNow +
          ", "+english.共 + " " +
          this.totalZhMoney +
          english.元宝+"</p>",
        scrollable: true,
        loading: true,
        onOk: () => {
          const agentData = {
            token: this.token,
            commandid: "3019",
            nickname: JSON.parse(localStorage.getItem("userData")).nickname,
            data: this.zhBetData,
            win_stop: this.zhType ? 1 : 0,
            avatar: JSON.parse(localStorage.getItem("userData")).avatar
          };
          websocketsend(agentData);
          this.showZhDlg = false;
        }
      });
    },
    checkOdds(index, title, type) {
      //选中操作
      if (type != undefined) {
        index = type + "_" + index;
      }
      this.checkedOddIndex = index;
      if (this.checkedOdd.indexOf(index) == -1) {
        this.checkedOdd.push(index);
        this.oddsWay.push(title);
      } else {
        for (var i = 0; i < this.checkedOdd.length; i++) {
          if (this.checkedOdd[i] == index) {
            this.checkedOdd.splice(i, 1);
            this.oddsWay.splice(i, 1);
          }
        }
      }
      this.realCkedOdd = this.checkedOdd;
      this.totalMoney = this.realCkedOdd.length * this.checkedMoney;
    },
    checkLhcOdds(index, title) {
      //连码选中操作emm..
      var str1 = title.split("_")[0];
      var str2 = title.split("_")[1];
      if (this.chekedMethod == 9) {
        //连尾要去掉尾字QAQ
        str2 = str2.substr(0, 1);
      }
      if (this.checkedOdd.indexOf(index) == -1) {
        if (this.isZhuiHao == true) {
          if (this.realCkedOdd.length == 1) {
            this.$Message.warning(english.追号只能选择一注+"~");
            return;
          }
        }
        if (this.chekedMethod == 3) {
          if (this.checkedOdd.length == 10) {
            this.$Message.warning(english.最多选择10个号码+"~");
            return;
          }
        }
        if (this.chekedMethod == 8 || this.chekedMethod == 9) {
          if (this.checkedOdd.length == 8) {
            this.$Message.warning(english.最多选择8个号码+"~");
            return;
          }
        }
        if (this.chekedMethod == 10) {
          if (this.chekedMethod1 == 0) {
            if (this.checkedOdd.length == 8) {
              this.$Message.warning(english.最多选择8个号码+"~");
              return;
            }
          } else if (this.chekedMethod1 == 1) {
            if (this.checkedOdd.length == 9) {
              this.$Message.warning(english.最多选择9个号码+"~");
              return;
            }
          } else if (this.chekedMethod1 == 2) {
            if (this.checkedOdd.length == 10) {
              this.$Message.warning(english.最多选择10个号码+"~");
              return;
            }
          } else if (this.chekedMethod1 == 3) {
            if (this.checkedOdd.length == 11) {
              this.$Message.warning(english.最多选择11个号码+"~");
              return;
            }
          } else if (this.chekedMethod1 == 4) {
            if (this.checkedOdd.length == 12) {
              this.$Message.warning(english.最多选择12个号码+"~");
              return;
            }
          } else if (this.chekedMethod1 == 5) {
            if (this.checkedOdd.length == 13) {
              this.$Message.warning(english.最多选择13个号码+"~");
              return;
            }
          }
        }
        this.lmTitleArr.push(str2);
        this.checkedOdd.push(index);
      } else {
        for (var i = 0; i < this.checkedOdd.length; i++) {
          if (this.checkedOdd[i] == index) {
            this.checkedOdd.splice(i, 1);
            this.lmTitleArr.splice(i, 1);
          }
        }
      }
      var realZhuMany;
      if (this.chekedMethod == 3) {
        //连码
        if (this.chekedMethod1 == 0 || this.chekedMethod1 == 1) {
          realZhuMany = this.getRealZhu(this.checkedOdd.length, 3);
        } else {
          realZhuMany = this.getRealZhu(this.checkedOdd.length, 2);
        }
      }
      if (this.chekedMethod == 8 || this.chekedMethod == 9) {
        //连肖
        if (this.chekedMethod1 == 0 || this.chekedMethod1 == 3) {
          realZhuMany = this.getRealZhu(this.checkedOdd.length, 2);
        } else if (this.chekedMethod1 == 1 || this.chekedMethod1 == 4) {
          realZhuMany = this.getRealZhu(this.checkedOdd.length, 3);
        } else if (this.chekedMethod1 == 2 || this.chekedMethod1 == 5) {
          realZhuMany = this.getRealZhu(this.checkedOdd.length, 4);
        }
      }
      if (this.chekedMethod == 10) {
        //不中
        if (this.chekedMethod1 == 0) {
          realZhuMany = this.getRealZhu(this.checkedOdd.length, 5);
        } else if (this.chekedMethod1 == 1) {
          realZhuMany = this.getRealZhu(this.checkedOdd.length, 6);
        } else if (this.chekedMethod1 == 2) {
          realZhuMany = this.getRealZhu(this.checkedOdd.length, 7);
        } else if (this.chekedMethod1 == 3) {
          realZhuMany = this.getRealZhu(this.checkedOdd.length, 8);
        } else if (this.chekedMethod1 == 4) {
          realZhuMany = this.getRealZhu(this.checkedOdd.length, 9);
        } else if (this.chekedMethod1 == 5) {
          realZhuMany = this.getRealZhu(this.checkedOdd.length, 10);
        }
      }
      this.realCkedOdd = [];
      for (var i = 0; i < realZhuMany; i++) {
        this.realCkedOdd.push(i);
      }
      this.oddsWay[0] = str1 + "_" + this.lmTitleArr;
      this.totalMoney = this.realCkedOdd.length * this.checkedMoney;
    },
    getRealZhu(a, b) {
      //连码算法
      var topNum = 1;
      for (var i = a; i > a - b; i--) {
        topNum = topNum * i;
      }
      var botNum = 1;
      for (var j = 1; j <= b; j++) {
        botNum = botNum * j;
      }
      let dataSum = topNum / botNum;
      return dataSum;
    },
    checkMoney(index, item) {
      if (item == english.梭哈) {
        let accountData = this.accountData.money_usable;
        this.checkedMoney = Math.round(Number(accountData));
        this.checkedMoneyIdx = index;
        this.totalMoney = this.checkedMoney * this.realCkedOdd.length;
      } else {
        this.checkedMoney = item;
        this.checkedMoneyIdx = index;
        this.totalMoney = this.checkedMoney * this.realCkedOdd.length;
      }
    },
    resetOdds() {
      this.zhBetData = [];
      this.zhTableData = [];
      this.manyBei = 1;
      this.manyQi = 1;
      this.checkedOddIndex = -1;
      this.checkedOdd = [];
      this.realCkedOdd = [];
      this.lmTitleArr = [];
      this.oddsWay = [];
      this.checkedMoney = this.moneyList[0];
      this.checkedMoneyIdx = 0;
      this.totalMoney = 0;
    },
    closeOddsDlg: function() {
      this.totalMoney = this.checkedMoney * this.realCkedOdd.length;
      this.sendOddsDlg = false;
    },
    openOddsDlg: function() {
      let newMoneyArr = [];
      let newOddsArr = [];
      for (var i = 0; i < this.oddsWay.length; i++) {
        newOddsArr.push({
          name: this.methodsArr[this.chekedMethod],
          type: this.oddsWay[i],
          money: this.checkedMoney,
          remove: english.删除
        });
      }
      for (var j = 0; j < this.realCkedOdd.length; j++) {
        newMoneyArr.push(this.checkedMoney);
      }
      this.moneyArr = newMoneyArr;
      this.oddsTableData = newOddsArr;
      this.sendOddsDlg = true;
    },
    sendOdds: function() {
      this.sendFlag=true
      setTimeout(()=>{
        this.sendFlag=false
      },1000)
      var money = [];
      var single_money = []
      if (
        this.chekedMethod == 3 ||
        this.chekedMethod == 8 ||
        this.chekedMethod == 9 ||
        this.chekedMethod == 10
      ) {
        money[0] = this.totalMoney;
        single_money=[this.checkedMoney]
      } else {
        money = this.moneyArr;
        single_money=this.moneyArr
      }
      const agentData = {
        token: this.token,
        commandid: "3006",
        nickname: JSON.parse(localStorage.getItem("userData")).nickname,
        way: this.oddsWay,
        money: money,
        single_money: single_money,
        avatar: JSON.parse(localStorage.getItem("userData")).avatar
      };
      var way = this.oddsWay
      var uniNo = new Date().getTime()+Math.floor(Math.random()*900)+100
      var params = {
        user_id: this.userInfo.id,
        lottery_type: this.$route.query.lottery_type,
        room_id: this.$route.query.id,
        nickname: this.userInfo.nickname,
        money: JSON.stringify(money),
        issue: this.issue,
        way: JSON.stringify(way),
        single_money: JSON.stringify(single_money),
        serTime: this.serTime,
        uniNo: uniNo,
        token: this.token,
      }
      let lotRstData={
        isHttp: true,
        isLoading: true,
        avatar: this.userInfo.avatar,
        commandid: 30007,
        issue: this.issue,
        money: money,
        single_money: single_money,
        nickname: this.userInfo.nickname,
        time: timeFormat(new Date().getTime()),
        total_money: this.totalMoney,
        total_zushu: this.realCkedOdd.length,
        uid: this.userInfo.id,
        uniNo: uniNo,
        sort: this.userInfo.honor.sort,
        serTime: this.serTime,
        order_no: '',
        way: way,
        loadingFailed: false,
        failedText: '',
      }
      this.$store.commit('setLotRstData', lotRstData)
      this.$http.post(this.urlRequest + "?m=api&c=workerman&a=http_betting", params).then(result=>{
        result=result.data
        if(result.commandid!=3007){
          var failedObj = JSON.parse(JSON.stringify(lotRstData))
          failedObj.loadingFailed=true
          failedObj.failedText=result.content
          this.$store.commit('setNewRstData', failedObj)
          return
        }
        var accountData = this.accountData
        accountData.money_usable=result.current_money
        this.$store.commit('setAccountData', accountData)
        this.$store.commit('setNewRstData', result)
      },
      err=>{
        lotRstData.loadingFailed=true
        lotRstData.failedText=english.投注超时+', '+english.请重新下注
        this.$store.commit('setNewRstData', lotRstData)
      })
      // websocketsend(agentData);
      this.sendOddsDlg = false;
    },
    minBetAdd() {
      // 最小投注的事件
      this.checkedMoney = Number(this.betLower);
      this.totalMoney = this.checkedMoney * this.realCkedOdd.length;
      this.checkedMoneyIdx = null;
    }
  },
  components: {},
  watch: {
    oddsData: function(oldVal, newVal) {
      let f1Room = oldVal;
      let that = this;
      if (!_.isEmpty(f1Room)) {
        for (var i = 0; i < that.wanfOdds.length; i++) {
          if (!_.isEmpty(f1Room.panel_1) && that.wanfOdds[i].name == english.特码) {
            that.wanfOdds[i].panel = f1Room.panel_1;
          } else if (
            !_.isEmpty(f1Room.panel_2) &&
            that.wanfOdds[i].name == english.正码
          ) {
            that.wanfOdds[i].panel = f1Room.panel_2;
          } else if (
            !_.isEmpty(f1Room.panel_3) &&
            that.wanfOdds[i].name == english.正特
          ) {
            that.wanfOdds[i].panel = f1Room.panel_3;
          } else if (
            !_.isEmpty(f1Room.panel_4) &&
            that.wanfOdds[i].name == english.连码
          ) {
            that.wanfOdds[i].panel = f1Room.panel_4;
          } else if (
            !_.isEmpty(f1Room.panel_5) &&
            that.wanfOdds[i].name == english.半波
          ) {
            that.wanfOdds[i].panel = f1Room.panel_5;
          } else if (
            !_.isEmpty(f1Room.panel_6) &&
            that.wanfOdds[i].name == english.尾数
          ) {
            that.wanfOdds[i].panel = f1Room.panel_6;
          } else if (
            !_.isEmpty(f1Room.panel_7) &&
            that.wanfOdds[i].name == english.一肖
          ) {
            that.wanfOdds[i].panel = f1Room.panel_7;
          } else if (
            !_.isEmpty(f1Room.panel_8) &&
            that.wanfOdds[i].name == english.特肖
          ) {
            that.wanfOdds[i].panel = f1Room.panel_8;
          } else if (
            !_.isEmpty(f1Room.panel_9) &&
            that.wanfOdds[i].name == english.连肖
          ) {
            that.wanfOdds[i].panel = f1Room.panel_9;
          } else if (
            !_.isEmpty(f1Room.panel_10) &&
            that.wanfOdds[i].name == english.连尾
          ) {
            that.wanfOdds[i].panel = f1Room.panel_10;
          } else if (
            !_.isEmpty(f1Room.panel_11) &&
            that.wanfOdds[i].name == english.不中
          ) {
            that.wanfOdds[i].panel = f1Room.panel_11;
          } else if (
            !_.isEmpty(f1Room.panel_12) &&
            that.wanfOdds[i].name == english.正码6
          ) {
            that.wanfOdds[i].panel = f1Room.panel_12;
          } else if (
            !_.isEmpty(f1Room.panel_13) &&
            that.wanfOdds[i].name == english.正6龙虎
          ) {
            that.wanfOdds[i].panel = f1Room.panel_13;
          }
          if (_.isEmpty(that.wanfOdds[i].panel)) {
            that.wanfOdds.splice(i, 1);
            i--;
          }
        }
      }
      this.playMethod = that.wanfOdds[0].panel[0];
    },
    lDragonData: function(newVal, oldVal){
      if(newVal){
        for(var i=0;i<this.wanfOdds.length;i++){
          if(this.wanfOdds[i].name==english.长龙投注){
            this.wanfOdds.splice(i,1)
            this.methodsArr.splice(i,1)
          }
        }
        if(newVal.length>0){
          this.wanfOdds.push({
            name:english.长龙投注,
            panel:newVal
          })
          this.methodsArr.push(english.长龙投注)
          if(this.methodsArr[this.chekedMethod]==english.长龙投注){
            this.playMethod=newVal
          }
        }
      }
    },
    timeMessages: function(oldVal, newVal) {
      this.issue = oldVal.issue;
    },
    cRoomData: function(oldVal, newVal) {
      let dataArr = oldVal.p_room;
      for (var i = 0; i < dataArr.length; i++) {
        if (this.roomId == dataArr[i].id||this.roomId == dataArr[i].idd) {
          this.betLower = dataArr[i].lower;
          if (dataArr[i].odds_exp == undefined || dataArr[i].odds_exp == "") {
            this.RoomDateList = english.暂无数据;
          } else {
            this.RoomDateList = dataArr[i].odds_exp;
          }
        }
      }
    },
    BetmoneyList: function(oldVal, newVal) {
      // let arr = []
      // for(var i=0; i<oldVal.length; i++){
      //   if(oldVal[i]>=1000){
      //       arr = arr.push(Number(oldVal[i]) / 1000 + 'K')
      //   }else{
      //       arr.push(Number(oldVal[i]))
      //   }
      // }
      this.moneyList = oldVal;
      this.checkedMoney = this.moneyList[0];
    },
    checkedMoney: function(newVal, oldVal) {
      this.totalMoney = this.realCkedOdd.length * newVal;
    }
  },
  deactivated() {},
  beforeDestroy() {}
};
