  border-right: 0px;
  border-top-left-radius: 6px;
  border-bottom-left-radius: 6px;
  position: absolute;
  top: 50%;
  right: 0;
  margin-top: -20px;
  width: 15px;
  height: 40px;
  /* background: url("../../../../assets/images/spirt.png") 0 178px; */
  cursor: pointer;
}
.choosedactive {
  background-color: #ececec;
  border-left: 4px solid red;
}
.activebottomline {
  border-bottom: 4px solid red;
  color: #545454 !important;
  font-weight: 600;
}
.tableTip {
  text-align: left;
  float: left;
  line-height: 32px;
}
.tableTip span {
  color: #f3596a;
  padding: 0px 4px;
}
.zfInput {
  text-align: center;
  margin-bottom: 10px;
  margin-top: 20px;
}
.zfInput .ivu-input-number {
  margin: 0px 5px;
}
.moneyInput {
  width: 80%;
  margin: 0 auto;
  display: block;
  margin-bottom: 10px;
}
.zhPanel2 {
  margin-bottom: 16px;
}
.zhPanel2 .ivu-input-number {
  margin: 0px 5px;
}

.moneyInput2 {
  width: 160px;
  margin: 0px 10px;
}
.resetBtn {
  border: none;
  background-color: white;
  color: #5db2ff;
}
.btnBox button {
  display: block;
  margin: 0 auto;
}
.tableNoData {
  height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.leftBar {
  display: flex;
  width: 412px
}
.zjjtP {
  text-align: right;
  padding: 8px 4px;
}
.cszPanel {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
}
.cszStyle {
  width: 30%;
  min-height: 50px;
  margin-left: 3%;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  align-items: center;
  margin-top: 10px;
  border: solid 1px #c7c7c7;
  border-radius: 3.2px;
  cursor: pointer;
}
.cszStyle p {
  color: #f3596a;
}
.cszBox {
  border-radius: 50%;
  border: 1px solid #c7c7c7;
  text-align: center;
  width: 30px;
  height: 30px;
  line-height: 30px;
  box-sizing: border-box;
  font-size: 16px;
  font-weight: bold;
  display: block;
}
.bbPanel {
  width: 100%;
}
.bbPanel > div {
  border: 1px solid #c7c7c7;
  cursor: pointer;
  border-radius: 6px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  padding: 5px 10px;
  padding-top: 0px;
}
.bbPeiLv {
  color: #f3596a;
  display: block;
}
.wsPeiLv {
  color: #f3596a;
}
.bbPanel > div div:first-child {
  margin-top: 5px;
}
.bbPanel > div div:last-child {
  display: flex;
  flex-wrap: wrap;
  max-width: 80%;
  justify-content: flex-start;
}
.bbPanel > div div:last-child span {
  margin-left: 5px;
  margin-top: 5px;
}
.zmPanel {
  width: 100%;
}
.zmTitle {
  color: #ff8624;
  margin-top: 10px;
  text-align: center;
}
